//import './Home.css';
import { Routes, Route, Link } from "react-router-dom";

import Home from "./Home";
import CadastroProduto from "./CadastroProduto";
import CadastroUsuario from "./CadastroUsuario";
import CarrinhoCompras from "./CarrinhoCompras";
import ListaProduto from "./ListaProduto";
import LoginUsuario from "./LoginUsuario";

function Rotas() {
  return (
    <>
      <Routes>
        <Route path="/" element={<Home />}>
          <Route path="/lista_produto" element={<ListaProduto />} />
          <Route path="/login_usuario" element={<LoginUsuario />} />
        </Route>
        <Route path="carrinho_compras" element={<CarrinhoCompras />} />
        <Route path="/cadastro_usuario" element={<CadastroUsuario />} />

        
        <Route path="/cadastro_produto" element={<CadastroProduto />} />
      </Routes>
      
    </>
  );
}

export default Rotas;
